body {
  background-color: #f7f7f7;
  font-family: 'Open Sans', sans-serif;
}

h1, h2, h3, h4, h5, h6 {
  font-family: 'Aleo', serif;
}


.brandImage {
    width: 8rem;
}

.navbar {
    background-color: white;
    height: 80px;
}

.nav-link {
    color:black!important;
}

.nav-link:hover {
    color: #97442f!important;
}



button:hover {
    cursor: pointer;
}


.image-gallery-thumbnail {
    width: 75px; 
}

.image-gallery-thumbnail img {
    width: auto;
    height: 75px;
}

.image-gallery-thumbnail.active {
    border: #717175 1px solid;
    width: auto;
}

.image-gallery-image {
    width: auto;
    max-height: 500px;
}

.image-gallery-left-nav {
    font-size: 3rem;
}

.image-gallery-right-nav {
    font-size: 3rem;
}


#home-logo {
    float:right;
    width: 17rem;
}

a {
    color: #97442f;
}
  
a:hover {
    text-decoration: none;
    color:#356377!important;
}
  
#more.white-text {
    color: white;
}

#more.white-text:hover {
    color: white!important;
    font-style: italic;
}

#sidebar {
    background-color: #97442f;
}

.card {
    /* border: 1px solid white; */
}

.card-img-top {
    height: 9rem;
    object-fit: cover;
}

.card-body {
    padding: 0;
}


.btn-sponsor {
    background-color: #97442f;
    border: none;
}

.card {
    background-color: white;
}

.kidsListPic {
    height: 14rem;
    object-fit: cover;
}

.learn {
    line-height:2.35rem;
}


.btn-search {
    background-color: #356377;
    border: none;
}

.label {
    background-color: #356377;
}

.label-text {
    color: white;
}
